.slick-slider {
  position: relative;

  display: block;
  box-sizing: border-box;

  user-select: none;

  -webkit-touch-callout: none;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;

  display: block;
  overflow: hidden;

  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;

  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track::before,
.slick-track::after {
  display: table;

  content: "";
}

.slick-track::after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;

  height: 100%;
  min-height: 1px;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;

  height: auto;

  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slick {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    padding: 0;
  }
}

.slick__item {
  @media (min-width: $desktop-width) {
    width: 226px;
    margin-right: 25px;

    &:last-of-type {
      margin-right: 0;
    }
  }
}

.slick__dot {
  display: flex;
  margin: 0 auto;
  padding: 0;
  list-style: none;

  border: none;
}

.slick__dot button {
  border: none;
  background-color: #cccccc;
  border-radius: 50%;
  width: 10px;
  height: 11px;
  margin-right: 14px;
  margin-top: 6px;
  color: transparent;

  cursor: pointer;

  &:active,
  &:focus {
    width: 14px;
    height: 14px;
    background-color: #0062ff;
    outline: none;
  }
}

.slick-active button {
  width: 14px;
  height: 14px;
  background-color: #0062ff;
}
